import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { Avatar, Button, Col, Layout, Menu, Popover, Row } from 'antd'
import {
  HeartOutlined,
  InboxOutlined,
  MenuFoldOutlined,
  MenuUnfoldOutlined,
  ShoppingCartOutlined,
  UserOutlined,
} from '@ant-design/icons'
import { useDispatch, useSelector } from 'react-redux'
import { selectCurrentUserLogin, selectIsAuth } from '../../../../redux/auth-selecors'
import { logout } from '../../../../redux/auth-reducer'
import { Input } from 'antd'
import './HeaderSmall.module.css'
import { Breakpoint } from 'react-socks'

const { Search } = Input

const onSearch = (value: any) => console.log(value)

const logo = require('../../../../assets/images/logo.jpg')

export type MapPropsType = {
  collapsed: any
  setCollapsed: any
  t: any
  i18n: any
}

export const HeaderSmall: React.FC<MapPropsType> = (props) => {
  const { t, i18n } = props
  const isAuth = useSelector(selectIsAuth)
  const login: any = useSelector(selectCurrentUserLogin)

  const dispatch = useDispatch()

  const logoutCallback = () => {
    // @ts-ignore
    dispatch(logout())
  }

  const { Header } = Layout

  return (
    <Breakpoint small down>
      <Header
        className="site-layout-background"
        style={{
          padding: 0,
          width: '100%',
        }}
      >
        <Row gutter={[16, 16]}>
          <Col span={5} style={{ marginTop: '3px' }}>
            <div className="logo">
              <Link to={'/Webshop/'}>
                <img src={logo} alt={'some text'} style={{ width: '30px', height: '30px' }}></img>
              </Link>
            </div>
          </Col>
          <Col span={4} style={{ marginTop: '3px' }}>
            <Link to="/Webshop/">
              <InboxOutlined style={{ fontSize: '22px', color: '#08c' }} />
            </Link>
          </Col>
          <Col span={4} style={{ marginTop: '3px' }}>
            <Link to="/Webshop/">
              <HeartOutlined style={{ fontSize: '22px', color: '#08c' }} />
            </Link>
          </Col>
          <Col span={4} style={{ marginTop: '3px' }}>
            <Link to="/Webshop/">
              <ShoppingCartOutlined style={{ fontSize: '22px', color: '#08c' }} />
            </Link>
          </Col>
          {isAuth ? (
            <>
              {' '}
              <Col span={7}>
                <Avatar
                  alt={login || ''}
                  style={{ backgroundColor: '#87d068', marginRight: '25%' }}
                  icon={<UserOutlined />}
                />
                <Button onClick={logoutCallback} type={'primary'}>
                  <Link to="/Webshop/">Log out</Link>
                </Button>
              </Col>
            </>
          ) : (
            <Col span={7}>
              <Button type="primary" style={{ width: '92%', padding: '0px' }}>
                <Link to={'/Webshop/login/'}>{t('Login')}</Link>
              </Button>
            </Col>
          )}
        </Row>
      </Header>
    </Breakpoint>
  )
}
