import React from 'react'
import { MainLarge } from './Mains/Large/MainLarge'
import { MainMedium } from './Mains/Medium/MainMedium'
import { MainSmall } from './Mains/Small/MainSmall'

export type MapPropsType = {
  t: any
  i18n: any
}

export const Main: React.FC<MapPropsType> = (props) => {
  const { t, i18n } = props

  return (
    <>
      <MainLarge i18n={i18n} t={t} />
      <MainMedium i18n={i18n} t={t} />
      <MainSmall i18n={i18n} t={t} />
    </>
  )
}
