import React, { useState } from 'react'
import { Breakpoint } from 'react-socks'
import { Layout, Menu } from 'antd'
import { Link, Route, Switch } from 'react-router-dom'
import { LaptopOutlined, MenuOutlined, SearchOutlined, UserOutlined } from '@ant-design/icons'
import { Header } from '../../../Header/Header'
import Internationalization from '../../../Internationalization/Internationalization'
import { withSuspense } from '../../../../hoc/withSuspense'
import DialogsContainer from '../../../Dialogs/DialogsContainer'
import ProfileContainer from '../../../Profile/ProfileContainer'
import Advertisement from '../../../Products/Advertisement/Advertisement'
import UsersContainer from '../../../Users/UsersContainer'
import LoginPage from '../../../Login/Login'
import RegisterPage from '../../../Register/Register'
import CategoryPage from '../../../Products/Category/Category'

const { Content, Footer, Sider } = Layout

const SuspendedDialogs = withSuspense(DialogsContainer)
const SuspendedProfile = withSuspense(ProfileContainer)

//   <Menu.SubMenu key="sub1" icon={<UserOutlined />}></Menu.SubMenu>
// <Menu.SubMenu key="sub2" icon={<LaptopOutlined />}></Menu.SubMenu>
// <Menu.SubMenu key="sub3" icon={<MenuOutlined />}></Menu.SubMenu>

export const MainSmall = (props: any) => {
  const items = [
    {
      label: '',
      key: 'mail',
      icon: <UserOutlined />,
    },
    {
      label: '',
      key: 'search',
      icon: <SearchOutlined />,
    },
    {
      label: '',
      key: 'app',
      icon: <LaptopOutlined />,
    },
    {
      label: '',
      key: 'SubMenu',
      icon: <MenuOutlined />,
    },
  ]
  const [collapsed, setCollapsed] = useState(true)
  const { t, i18n } = props
  const [current, setCurrent] = useState('mail')
  const onClick = (e: any) => {
    console.log('click ', e)
    setCurrent(e.key)
  }
  return (
    <Breakpoint small down>
      <Layout>
        <Content style={{ padding: '0 10px' }}>
          <Layout className="site-layout">
            <Layout>
              <Header collapsed={collapsed} setCollapsed={setCollapsed} i18n={i18n} t={t} />
              <Content style={{ padding: '20px 20px', minHeight: '590px' }}>
                <Switch>
                  <Route exact path="/Webshop/" render={() => <Advertisement />} />

                  <Route path="/Webshop/dialogs/" render={() => <SuspendedDialogs />} />

                  <Route path="/Webshop/profile/:userId?/" render={() => <SuspendedProfile />} />

                  <Route path="/Webshop/users/" render={() => <UsersContainer />} />

                  <Route path="/Webshop/login/" render={() => <LoginPage />} />

                  <Route path="/Webshop/register/" render={() => <RegisterPage />} />

                  <Route path="/Webshop/catalog/" render={() => <CategoryPage />} />

                  <Route
                    path="/Webshop/internationalization/"
                    render={() => <Internationalization i18n={props.i18n} />}
                  />

                  <Route path="*" render={() => <div>404 NOT FOUND</div>} />
                </Switch>
              </Content>
            </Layout>
          </Layout>
        </Content>
        <div style={{ height: '100%', width: '100%' }}>
          <Footer
            style={{ position: 'relative', display: 'flex', justifyContent: 'center', height: '100%', width: '100%' }}
          >
            <Menu mode="horizontal" items={items} selectedKeys={[current]} onClick={onClick} />
          </Footer>
        </div>
      </Layout>
    </Breakpoint>
  )
}
